* {
    margin: 0%;
    padding: 0%;
}
.app{
    height: 100vh;
    .section{
        width: 100%;
        height: calc(100vh - 70px);
        background-color: coral;
        position: relative;
        top: 70px;
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        scrollbar-width: none; //for firefox
        &::-webkit-scrollbar{
            display: none;
        }

        > *{
            width: 100vw;
            height: calc(100vh - 70px);
            scroll-snap-align: start;
        }
    }
}