@import "../Global.scss";

.sidebar {
  width: 250px;
  height: 100vh;
  background-color: $myColor;
  position: fixed;
  top: 0;
  right: -300;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 500ms ease;

  &.active {
    right: 0;
  }

  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 1.5rem;
    font-weight: 500;
    color: $mainColor;
    width: 60%;

    li {
      margin-bottom: 50px;
      a {
        padding: 0% 7% 0% 0%;
        font-size: inherit;
        color: inherit;
        text-decoration: none;
      }
      &:hover {
         font-weight: bold;
        }
    }
  }
}
